<template>
  <div class="page">
    <the-navbar />
    <main class="home main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12 col-lg-9">
            <div class="row">
              <div class="col-12">
                <Skyler
                  :_id="page._id"
                  :description="page.description"
                  :geo="page.geo"
                  :image="page.image"
                  :name="page.name"
                  :slug="page.slug"
                />
              </div>
              <div class="col-12 col-sm-4 col-lg-4">
                <Presentation
                  :location="page.location"
                  :phone="page.phone"
                  :mobile="page.mobile"
                  :country="page.countryId"
                  :state="page.stateId"
                  :city="page.cityId"
                />
                <Events />
                <Followers />
              </div>
              <div class="col-12 col-sm-8 col-lg-8">
                <Publisher />
                <Post />
              </div>
            </div>
          </div>
          <div class="col-12 col-lg-3">
            <div class="row">
              <div class="col-12 col-sm-4 col-lg-12">
                <Adds />
              </div>
              <div class="col-12 col-sm-4 col-lg-12">
                <Featureds />
              </div>
              <div class="col-12 col-sm-4 col-lg-12">
                <Adds />
              </div>
              <div class="col-12">
                <the-footer />
              </div>
            </div>
          </div>
        </div>
      </div>
      <pre class="container" hidden>{{ $data }}</pre>
    </main>
  </div>
</template>

<script>
import { db } from "@/main";

import Adds from "@/components/Adds";
import Events from "@/components/Events";
import Featureds from "@/components/Featureds";
import Followers from "@/components/Followers";
import Post from "@/components/Post";
import Presentation from "@/components/Presentation";
import Publisher from "@/components/Publisher";
import Skyler from "@/components/Skyler";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

import axios from "axios";

export default {
  name: "Page",
  components: {
    Adds,
    Events,
    Featureds,
    Followers,
    Post,
    Presentation,
    Publisher,
    Skyler,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      page: ""
    };
  },
  created() {
    this.getPage();
  },
  methods: {
    async getPage() {
      await axios(`${db}/pages/${this.$route.params.id}`)
        .then(result => {
          if (result.data) {
            this.page = result.data;
          } else {
            this.$router.replace("/");
          }
        })
        .catch(error => {
          var errorCode = error.code;
          var errorMessage = error.message;
          if (errorCode == "auth/weak-password") {
            alert("The password is too weak");
          } else {
            alert(errorMessage);
          }
          console.log(error);
        });
    }
  },
  watch: {
    $route: "getPage"
  }
};
</script>

<style scoped></style>
